import React, { useContext, useEffect, useState } from "react";

import { InitContext } from "../contexts/initContext";
import "../styles/Register.scss";
import cities from "../jsons/cities.json";

function Register () {
    const { transactionUsersUrl } = useContext(InitContext)
    const [inputValue, setInputValue] = useState({      

    })
    const [alert, setAlert] = useState("")
    const [cityNo, setCityNo] = useState(0)

    const changeInput= (e) => {
        setInputValue({...inputValue, 
            [e.target.name] : e.target.value
        })
    }

    // const changeCity=()=>{
    //     console.log(e.target.name)
    //     console.log(e.target.value)
    // }

    const onSubmit= (e) => {
        e.preventDefault();
        console.log(inputValue)

        // fetch(transactionUsersUrl, {
        //     method: "POST",
        //     headers: {
        //         "Content-Type": "application/json"
        //     },
        //     body: JSON.stringify(inputValue)
        // })
        // .then(res=>res.json())
        // .then(data=>{
        //     console.log(data)
        //     setAlert(data.result)
        // })
        // .catch(err=>console.error(err))
        const data={...inputValue, action : "Add"}
        console.log(data)
        fetch(transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(data)
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
            if(data.result==="ok") setAlert("submit successfully :)")
        })
        .catch(err=>console.error(err))

    } 

    useEffect(()=>{
        fetch(transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                "action" : "Query",      
                // "userid": "123"
                "userid": ""
            })
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
        })
        .catch(err=>console.error(err))
    }, [])

    return(
        <main id="Register">
            <form onSubmit={onSubmit}>
                <h2>Login</h2>
                <div className="input-div">
                    <input name="userid" type="text" placeholder="user id" value={inputValue.userid} onChange={changeInput} required />
                    <input name="password" type="password" placeholder="password" value={inputValue.password} onChange={changeInput} required />
                    <input name="company" type="text" placeholder="company" value={inputValue.company} onChange={changeInput} />
                    <input name="username" type="text" placeholder="user name" value={inputValue.username} onChange={changeInput} />
                    <input name="title" type="text" placeholder="title" value={inputValue.title} onChange={changeInput} />
                    <input name="email" type="email" placeholder="email" value={inputValue.email} onChange={changeInput} required />
                    <input name="birthday" type="date" placeholder="birthday" value={inputValue.birthday} onChange={changeInput} />
                    <select name="gender" onChange={changeInput} defaultValue="">
                        <option value="">gender</option>
                        <option value="m">male</option>
                        <option value="f">female</option>
                    </select>
                    <input name="telephone" type="tel" placeholder="telephone" value={inputValue.telephone} onChange={changeInput} />
                    <input name="mobile" type="tel" placeholder="mobile" value={inputValue.mobile} onChange={changeInput} />                  
                    {/* <label>address
                        <select name="city" onChange={changeCity}>
                            {
                                cities.map((city, i)=><option value={city.CityName} key={i} >{city.CityName}</option>)
                            }
                        </select>
                        <select>
                            {
                                cities[cityNo].AreaList.map(area=><option>{`${area.ZipCode} ${area.AreaName}`}</option>)
                            }
                        </select>
                    </label> */}
                </div>
                    <div className="address-div">
                        <input name="zipcode" type="text" placeholder="zipcode" value={inputValue.zipcode} onChange={changeInput} />
                        <input name="address" type="text" placeholder="address" value={inputValue.address} onChange={changeInput} />                        
                    </div>

                    <input type="submit" value="sign up" />  
                {/* <input type="submit" value="next" disabled={alert.indexOf("not")===-1 ? false : true} /> */}
                {
                   ( alert.length>0 ) && <h1 className="alert" style={{background: alert.indexOf("ok")>0? "var(--green)": "var(--red)"}}>{alert}</h1>
                }
            </form>
        </main>
    )
}

export default Register;